import Mock from "mockjs";
import singer from "@/mock/singer";
import songList from "@/mock/songList";

//接口:相当于nodejs里面中间件
//第一个参数：接口的地址 第二个参数:向这个接口发请求获取到的数据
//Mock插件：中间件默认是GET请求
// Mock.mock("/mock/banner", { code: 200, data: banner });
Mock.mock("http://localhost:8081/singer/allSinger", singer);
Mock.mock("http://localhost:8081/songList/allSongList", songList);



